.content-section {
  padding-top: 15px;
}

.content-section--gray {
  background-color: $gray;
}

.content-section__title {
  @include width-container;
  text-align: left;
  font-size: 36px;
  line-height: 60px;
  font-weight: normal;
}

@media (min-width: $tablet-width) {
  .content-section {
    padding-top: 44px;
  }

  .content-section--gray {
    min-height: 1039px;
    background: linear-gradient(to bottom, $white 620px, rgba($white, 0) 620px), linear-gradient(to right, $light-gray-bg 50%, $dark-gray-bg 50%);
    border-top: 1px solid $gray;
  }

  .content-section__title {
    font-size: 60px;
  }
}

@media (min-width: $desktop-width) {
  .content-section {
    padding-top: 34px;
  }

  .content-section--gray {
    min-height: 597px;
    background: linear-gradient(to bottom, $white 168px, rgba($white, 0) 168px), linear-gradient(to right, $light-gray-bg 35%, $dark-gray-bg 65%);
    border-top: none;
  }
}
