.block-info {
  margin-bottom: 20px;
  background-color: $gray;
  padding: 31px 20px 23px;

  &:last-of-type {
    margin-bottom: 0;
  }
}

.block-info__title {
  margin: 0;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  font-weight: normal;
  padding-left: 70px;

  position: relative;
  text-align: left;

  &::before {
    position: absolute;
    left: 2px;
    top: -8px;
    content: "";
    border-radius: 50%;
    background-color: $green;
    height: 50px;
    width: 50px;
  }

  &--slim::after {
    position: absolute;
    left: 10px;
    top: -9px;
    content: "";
    width: 35px;
    height: 49px;
    background: url("../img/icon-cat-slim.svg") no-repeat;
  }

  &--pro::after {
    position: absolute;
    left: -6px;
    top: 2px;
    content: "";
    width: 67px;
    height: 28px;
    background: url("../img/icon-cat-pro.svg") no-repeat;
  }
}

.block-info__text {
  color: $shadow-gray;
  font-size: 14px;
  font-family: "Arial", sans-serif;
  line-height: 18px;
  margin-top: 29px;
  margin-bottom: 19px;
  padding-bottom: 21px;
  border-bottom: 1px solid rgba($black, 0.1);
}

.block-info__button {
  color: $black;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 16px;
  position: relative;
  padding-right: 35px;

  &:active {
    opacity: 0.3;
    transition: 0.3s;
  }

  &::after {
    content: url("../img/icon-right-arrow-long.svg");
    width: 24px;
    height: 11px;
    position: absolute;
    right: 0;
    top: 55%;
    transform: translateY(-100%);
  }

  &:hover,
  &:focus {
    &::after {
      width: 33px;
      transition: 0.2s;
      transform: translate(9px, -135%);
      content: url("../img/icon-right-arrow-verylong.svg");
    }
  }
}

@media (min-width: $tablet-width) {
  .block-info {
    box-sizing: border-box;
    min-height: 372px;
    padding: 76px 62px 62px 52px;
    margin-bottom: 30px;

    &:last-of-type {
      margin-bottom: 0;
    }
  }

  .block-info__title {
    width: 100%;
    font-size: 36px;
    text-align: left;
    padding: 0 200px 0 0;
    box-sizing: border-box;
    position: relative;

    &::before {
      position: absolute;
      left: auto;
      right: 0;
      top: 16px;
      content: "";
      border-radius: 50%;
      background-color: $green;
      height: 200px;
      width: 200px;
    }

    &--slim::after {
      position: absolute;
      left: auto;
      right: 30px;
      top: 4px;
      content: "";
      width: 140px;
      height: 194px;
      background: url("../img/icon-cat-slim.svg") no-repeat;
    }

    &--pro::after {
      position: absolute;
      left: auto;
      right: -34px;
      top: 60px;
      content: "";
      width: 268px;
      height: 112px;
      background: url("../img/icon-cat-pro.svg") no-repeat;
    }
  }

  .block-info__text {
    min-height: 95px;
    width: 384px;
    font-size: 16px;
    line-height: 24px;
    border-bottom: none;
    margin: 25px 0 35px;

    &--pro {
      width: 343px;
    }
  }

  .block-info__button {
    font-size: 20px;
    line-height: 20px;
    position: relative;
    padding-right: 48px;

    &::after {
      transform: translateY(-135%);
    }
  }
}

@media (min-width: $desktop-width) {
  .block-info {
    width: calc(50% - 40px);
    min-height: 372px;
    margin: 40px 0;
    padding: 52px 53px;
    box-sizing: border-box;

    &:last-of-type {
      margin-bottom: 40px;
    }
  }

  .block-info__title {
    padding: 24px 0 0 161px;
    position: relative;

    &::before {
      position: absolute;
      left: 0;
      right: auto;
      top: -5px;
      content: "";
      border-radius: 50%;
      height: 100px;
      width: 100px;
    }

    &--slim::after {
      position: absolute;
      left: 13px;
      right: auto;
      top: -11px;
      content: "";
      width: 70px;
      height: 97px;
      background: url("../img/icon-cat-slim.svg");
    }

    &--pro::after {
      position: absolute;
      left: -19px;
      right: auto;
      top: 17px;
      content: "";
      width: 134px;
      height: 56px;
      background-image: url("../img/icon-cat-pro.svg");
    }
  }

  .block-info__text {
    width: 100%;
    margin-bottom: 0;
    margin-top: 74px;
    padding-bottom: 7px;
  }
}
